import React, {Component} from 'react';
import Particles from "react-tsparticles";
import logo from './logo.svg';
import './App.css';
import particlesOptions from "./particles.json";
import Web3 from 'web3';
import {xoxo_ido_abi, xoxo_ido_address} from './config';
import { IconContext } from "react-icons";
import { FaTelegram } from 'react-icons/fa';

class App extends Component{
    componentWillMount() {
        this.loadBlockchainData()
      }
    
      async loadBlockchainData() {
        const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
        const network = await web3.eth.net.getNetworkType()
        const ethereum = window.ethereum;
        const enabledWeb3 = await ethereum.enable();
        const accounts = await web3.eth.getAccounts()
        this.setState({account: accounts[0]})
        const xoxoido = new web3.eth.Contract(xoxo_ido_abi, xoxo_ido_address)
        this.setState({xoxoido})
        //const BuyToken = await xoxoido.methods.buyTokens().call()
        //this.setState({BuyToken})
      }
      
      constructor(props){
        super(props)
        this.state = {
          account:'', 
        }
        this.Buyxoxo = this.Buyxoxo.bind(this)
      }
    
    
     Buyxoxo() {
        this.state.xoxoido.methods.buyTokens().send({from: this.state.account, value: this.amount.value* 10**18})
      }

render(){

    return (
        <div className="App">
        <Particles options={particlesOptions}/>
        <header className="App-header">
        <img src={logo} className="App-logo" alt="logo"/>

        <p>
                GET CP TOKEN NOW!
            </p>
            <p>
                
                <form onSubmit={(event) => {event.preventDefault()
                                            this.Buyxoxo()}}>
                    <input ref={(input) => {this.amount = input}}type="text" id="value" placeholder="Testnet BNB Amount" required />
                    <input classname="get-xoxo" type="submit" value= "GET" />
                  </form>

            </p>

            <IconContext.Provider value={{ color:"#2E9BB3", size:"3em", className: 'react-icons' }}>
                
                <div style={{flexDirection:'row'}}>
                <a href='https://t.me/CPFinance' target="_blank"><FaTelegram /></a>
                
                </div>
                
            </IconContext.Provider>
        
        </header>
        </div>
    );

}
    
}

export default App;
